import { Component, ViewContainerRef } from '@angular/core';
import { NgIf } from '@angular/common';
import { Router, Event, NavigationStart } from '@angular/router';

@Component({
    selector: 'vl-app',
    template: `
        <header *ngIf="showHeaderFooter()"></header>
        <div class="body"><router-outlet></router-outlet></div>
        <footer *ngIf="showHeaderFooter()"></footer>
    `
})

export class AppComponent {
     private viewContainerRef: ViewContainerRef;
    _currentInstruction: string = '';

    constructor(viewContainerRef: ViewContainerRef, private router: Router) {
        var self = this;
        self.viewContainerRef = viewContainerRef;
        self.router.events.subscribe(function(event: Event) {
            if(event instanceof NavigationStart) {
                self._currentInstruction = event.url;
            }
            // NavigationEnd
            // NavigationCancel
            // NavigationError
            // RoutesRecognized
        });
    }

    showHeaderFooter() {
        return this._currentInstruction.indexOf('signin') === -1;
    }
}
